var surveyJSON = {"pages":[{"name":"page1","elements":[{"type":"radiogroup","name":"question2","title":"  ","choices":[{"value":"item1","text":"19 "},{"value":"item2","text":"20~29"},{"value":"item3","text":"30~39"},{"value":"item4","text":"40~49"},{"value":"item5","text":"50~59"},{"value":"item6","text":"60 "}]},{"type":"checkbox","name":"question1","title":"   ()","choices":[{"value":"item1","text":"CMA"},{"value":"item2","text":""},{"value":"item3","text":""},{"value":"item4","text":""},{"value":"item5","text":""}],"colCount":2},{"type":"rating","name":"question3","title":"     .","rateMin":0,"minRateDescription":" ","maxRateDescription":""},{"type":"boolean","name":"question4","labelTrue": "","labelFalse": "\n","title":"  ?"},{"type":"comment","name":"question5","visibleIf":"{question4} = true","title":"  "}]}]}

