  function PdfMake(){
    /* documentDefinition : pdf      */
    var documentDefinition = {
      //content : pdf  
      content: [{
              text: 'First Title'
          }, //     
          {
              text: 'This is a content',
              bold: true
          }, //  bold 
          {
              text: 'hahaha',
              style: 'style_test'
          }, // style   style_test     
          {
              style: 'tableExample',
              table: {
                  widths: [100, '*', 200, '*'],
                  body: [
                      ['width=100', 'star-sized', 'width=200', 'star-sized'],
                      ['fixed-width cells have exactly the specified width', {
                          text: 'article1',
                          italics: true,
                          color: 'gray'
                      }, {
                          text: 'article2',
                          italics: true,
                          color: 'gray'
                      }, {
                          text: 'article3',
                          italics: true,
                          color: 'gray'
                      }]
                  ]
              }
          } // 
      ],
      //  /   
      footer: function (currentPage, pageCount) {
          return {
              margin: 10,
              columns: [{
                  fontSize: 9,
                  text: [{
                          text: '--------------------------------------------------------------------------' +
                              '\n',
                          margin: [0, 20]
                      },
                      {
                          text: '' + currentPage.toString() + ' of ' +
                              pageCount,
                      }
                  ],
                  alignment: 'center'
              }]
          };

      },
      //  
      styles: {
          style_test: {
              fontSize: 18,
              bold: true,
              margin: [0, 0, 0, 0],
              alignment: 'center'
          },
          tableExample: {
              margin: [0, 5, 0, 15]
          }
      },

      //           { width: number, height: number }
      pageSize: 'A4',

      /*   portrait :  , landscape :  */
      pageOrientation: 'portrait',
  };

  var pdf_name = 'pdf.pdf'; // pdf   
  pdfMake.createPdf(documentDefinition).download(pdf_name);
  }

  function SurveyPage(){
    return(
      <Survey.Survey 
        json={surveyJSON} 
        onComplete={PdfMake}
      />
    )
  }
export default  SurveyPage;