import React from 'react';
import '../css/Contact.css';

function Contact(){
    return(
        <div className="contact-container">
            <h1> </h1>
            <p>    !</p>
            <form className="contact-form" id="contact">
                <div className="form-container">
                    <div className="form-left"><input type="text" name="company" placeholder=""/></div>
                    <div className="form-right"><input type="text" name="name" placeholder=""/></div>                    </div>
                <div className="form-container">
                    <div className="form-left"><input type="text" name="call" placeholder=""/></div>
                    <div className="form-right"><input type="text" name="sort" placeholder=" "/></div>
                </div>
                <textarea className="contact-content" rows="12" cols="90" placeholder=" "></textarea> 
            </form>
            <button className="submit-btn" type="submit" form="contact"> </button>
        </div>
    );
}

export default Contact;